<template>
    <div class="orderDetailWarp">
        <header>
            <router-link to="order">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <section>
                <span>订单详情</span>
            </section>
        </header>
        <section class="scroll_container">
            <section class="scroll_insert">
                <section class="order_titel">
                    <img :src="'https://elm.cangdu.org/img/'+ tu_pian" alt="" style="border: .05rem solid #3190e8;border-radius: 50%;width: 3rem;height: 3rem;">
                    <p>{{title}}</p>
                    <p></p>
                    <router-link to="/shop">再来一单</router-link>
                </section>
                <section class="food_list">
                    <router-link to="" class="food_list_header">
                        <div class="shop_name" @click="goShop">
                            <img :src="'https://elm.cangdu.org/img/'+ tu_pian" alt="">
                            <span>{{shang_pu_name}}</span>
                        </div>
                        <i class="el-icon-arrow-right" style="font-size: .6rem"></i>
                    </router-link>
                    <ul class="food_list_ul">
                        <li class="food_list_ul_li" v-for="info in shang_pin_info[0]" :key="info._id">
                            <p class="food_name ellipsis">{{info.name}}</p>
                            <div class="quantity_price">
                                <span>X{{info.quantity}}</span>
                                <span>￥{{info.price}}</span>
                            </div>
                        </li>
                    </ul>
                    <div class="deliver_fee">
                        <span>配送费</span>
                        <span>4</span>
                    </div>
                    <div class="pay_ment">实付{{jia_ge}}.00</div>
                </section>
                <section class="order_detail_style">
                    <div>配送信息</div>
                    <section class="item_style">
                        <p class="item_left">送达时间：</p>
                        <div class="item_right">
                            <p>{{listorder.deliver_time}}</p>
                        </div>
                    </section>
                    <section class="item_style">
                        <p class="item_left">送货地址：</p>
                        <div class="item_right">
                            <p>{{listorder.consignee}}</p>
                            <p>{{listorder.addressDetail}}</p>
                            <p>{{listorder.phone}}</p>
                        </div>
                    </section>
                    <section class="item_style">
                        <p class="item_left">配送方式：</p>
                        <div class="item_right">
                            <p>蜂鸟专送</p>
                        </div>
                    </section>
                </section>
                <section class="order_detail_style">
                    <div>订单信息</div>
                    <section class="item_style">
                        <p class="item_left">订单号：</p>
                        <div class="item_right">
                            <p>{{ding_dan_hao}}</p>
                        </div>
                    </section>
                    <section class="item_style">
                        <p class="item_left">支付方式：</p>
                        <div class="item_right">
                            <p>{{listorder.pay_method}}</p>
                        </div>
                    </section>
                    <section class="item_style">
                        <p class="item_left">下单时间：</p>
                        <div class="item_right">
                            <p>{{xia_dan_time}}</p>
                        </div>
                    </section>
                </section>
            </section>
        </section>
    </div>
</template>

<script>
    export default {
        name: "OrderDetail",
        data(){
            return {
                listorder:[],
                ding_dan_hao:'',//订单号
                xia_dan_time:'',//下单时间
                title:'',//上面的表头
                shang_pin_info:[],
                shang_pu_name:'',
                jia_ge:'',
                tu_pian:'',
            }
        },
       created(){
            this.user_id = localStorage.getItem("user_id")
           this.ding_id = localStorage.getItem("dingId")
            this.axios.get("/bos/v1/users/"+this.user_id+"/orders/"+this.ding_id+"/snapshot").then(res=>{
                console.log(res)
                this.listorder = res
                console.log(res._doc)
                this.ding_dan_hao = res._doc.unique_id;
                this.xia_dan_time = res._doc.formatted_created_at
                this.title = res._doc.status_bar.title
                console.log(res._doc.basket.group)
                this.shang_pin_info = res._doc.basket.group;
                this.shang_pu_name = res._doc.restaurant_name
                this.jia_ge = res._doc.total_amount
                this.tu_pian = res._doc.restaurant_image_url
            }).catch(err=>{})
       },
        methods:{
            goShop(){
                this.$router.push({path:'/shop'})
            }
        }
    }
</script>

<style scoped>
    .orderDetailWarp{
        padding-top: 1.95rem;
        /*background-color: azure;*/
    }
    header {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>a{
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
        color: #fff;
    }
    header>section{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    header>section span{
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }

    .scroll_container{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding-top: 1.95rem;
    }
    .scroll_insert{
        padding-bottom: 3rem;
        transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
        transition-duration: 0ms;
        transform: translate(0px, -46px) scale(1) translateZ(0px);
    }
    .order_titel{
        pointer-events: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: .7rem;
        background-color: #fff;
        margin-bottom: .5rem;
    }
    .order_titel>:nth-child(2){
        font-size: .9rem;
        color: #333;
        font-weight: 700;
        margin-top: .4rem;
    }
    .order_titel>:nth-child(3){
        font-size: .55rem;
        color: #999;
        width: 10rem;
        margin-top: .3rem;
        text-align: center;
    }
    .order_titel>:nth-child(4){
        font-size: .6rem;
        color: #3190e8;
        margin-top: .5rem;
        border: .025rem solid #3190e8;
        padding: .15rem .4rem;
        border-radius: .1rem;
        text-decoration: none;
    }
    .food_list{
        background-color: #fff;
        pointer-events: auto;
    }
    .food_list_header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .2rem .5rem;
        border-bottom: 1px solid #f5f5f5;
        text-decoration: none;
    }
    .shop_name{

    }
    .shop_name img{
        width: 1.2rem;
        height: 1.2rem;
        vertical-align: middle;
        margin-right: .2rem;
    }
    .shop_name span{
        font-size: .75rem;
        color: #333;
        font-weight: 700;
    }
    .food_list_ul_li{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .5rem;
        line-height: 2rem;
    }
    .food_name{
        font-size: .6rem;
        color: #666;
        flex: 4;
    }
    .ellipsis{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .quantity_price{
        flex: 1;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .quantity_price>:nth-child(1){
        font-size: .6rem;
        color: #ccc;
    }
    .quantity_price>:nth-child(2){
        font-size: .6rem;
        color: #666;
    }
    .deliver_fee{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .5rem;
        line-height: 2rem;
        border-top: 1px solid #f5f5f5;
    }
    .deliver_fee span{
        font-size: .6rem;
        color: #666;
    }
    .pay_ment{
        font-size: .6rem;
        color: #fb6b23;
        border-top: 1px solid #f5f5f5;
        font-weight: 700;
        line-height: 2rem;
        text-align: right;
        padding-right: .5rem;
    }

    .order_detail_style{
        background-color: #fff;
        margin-top: .5rem;
        pointer-events: auto;
    }
    .order_detail_style>div{
        font-size: .75rem;
        color: #333;
        padding: .5rem;
        border-bottom: 1px solid #f5f5f5;
    }
    .item_style{
        display: flex;
        padding: .5rem;
    }
    .item_left{
        font-size: .65rem;
        color: #666;
        line-height: 1rem;
    }
    .item_right{

    }
    .item_right p{
        font-size: .65rem;
        color: #666;
        line-height: 1rem;
    }


</style>